
.scroll-list-wrapper {
  position: relative;
  height: 100%;
  /*position: absolute*/
  /*left: 0*/
  /*top: 0*/
  /*right: 0*/
  /*bottom: 0*/
  overflow: hidden;
  background: transparent;
}
.scroll-list-wrapper .scroll-content {
    position: relative;
    z-index: 1;
}
.scroll-list-wrapper .list-content {
    position: relative;
    z-index: 10;
    background: transparent;
}
.scroll-list-wrapper .list-content .list-item {
      font-size: 12px;
      padding-left: 20px;
      border-bottom: 1px solid #e5e5e5;
}
.pulldown-wrapper {
  position: absolute;
  width: 100%;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  transition: all;
}
.pulldown-wrapper .after-trigger {
    margin-top: 10px;
}
.pullup-wrapper {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 16px 0;
}



