
.agree-checkbox .mint-checklist-title {
  display: none;
}
.agree-checkbox .mint-checklist-label {
  padding: 0;
}
.agree-checkbox .mint-checkbox-core {
  border-radius: 4px;
  width: 18px;
  height: 18px;
  position: absolute;
}
.agree-checkbox .mint-checkbox-core::after {
    top: 2px;
    left: 5px;
}
.agree-checkbox .mint-checkbox-label {
  display: block;
  margin-left: 24px;
}



