
.bank-card-promotion-dialog .dialog-box {
  position: relative;
  width: 100%;
  min-width: 300px;
  margin: 0 auto;
  background: transparent;
  height: 100%;
}
.bank-card-promotion-dialog .dialog-body {
  height: 60%;
}
.bank-card-promotion-dialog .dialog-body .tip-content {
    height: 100%;
}
.bank-card-promotion-dialog .dialog-footer {
  position: absolute;
  width: 100%;
  height: 40%;
  top: 60%;
  background: #fff;
}
.bank-card-promotion-dialog .promotion-desc {
  line-height: 16px;
  padding: 10px;
  text-align: left;
}
.bank-card-promotion-dialog .promotion-url-section {
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
}
.bank-card-promotion-dialog .promotion-url-section input {
    -ms-flex: 60%;
        flex: 60%;
    border: 1px solid #999;
    margin-right: 10px;
    border-radius: 4px;
}
.bank-card-content {
  width: 100%;
}
.bank-card-content img {
    max-width: 100%;
    height: auto;
    display: block;
}



