
.dialog-enter-to,
.dialog-leave-to {
  transition: opacity .3s;
}
.dialog-enter,
.dialog-leave-to {
  opacity: 0;
}
.dialog-enter > .dialog-box,
.dialog-leave-to > .dialog-box {
  transform: translateY(-50%) scaleZ(1);
}
.dialog-enter-to > .dialog-box,
.dialog-leave-to > .dialog-box {
  transition: all .3s;
}
.dialog-cover {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 900;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
}
.dialog-box {
  position: relative;
  width: 30%;
  min-width: 300px;
  margin: 0 auto;
  background: #FFF;
}
.dialog-close-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 2em;
  height: 2em;
  opacity: .7;
  background: url(data:image/svg+xml;base64,) no-repeat 50% 50%;
  background-size: 50%;
  cursor: pointer;
}
.dialog-close-btn:hover {
  opacity: 1;
}
.dialog-body {
  min-height: 40px;
}
.dialog-header {
  border-bottom: 1px solid #eee;
  line-height: 30px;
  min-height: 40px;
  padding-left: 10px;
}
.dialog-content {
  padding: 12px 10px;
}
.dialog-footer {
  text-align: right;
  min-height: 40px;
  line-height: 40px;
}
.dialog-btn {
  margin: 0 20px;
  font-size: 14px;
}
.dialog-btn:last-child {
  margin-right: 0;
}
.dialog-ok-btn {
  color: #c30911;
}
.dialog-tip-cover {
  background: transparent;
  pointer-events: none;
}
.dialog-tip-cover > .dialog-box > .dialog-body {
  border-radius: 3px;
  color: #FFF;
  background: rgba(0, 0, 0, 0.8);
}



