
::-webkit-scrollbar {
  display: none;
}
* {
  box-sizing: border-box;
}
html, body {
  position: relative;
  width: 100%;
  height: 100%;
  font-size: 14px;
}

/*
[data-dpr="1"] body {
  font-size: 12px;
}
[data-dpr="2"] body {
