a[href] {
  color: #3eb7f3;
  text-decoration: none;
}
a[href].mint-cell {
    color: inherit;
}
p {
  padding: 0;
  margin: 0;
}
input[disabled], input:disabled {
  background-color: #fff;
  color: #37474f;
  -webkit-text-fill-color: #37474f;
  opacity: 1;
}
.mint-button--primary {
  background-color: #3eb7f3;
}
input, label[for], textarea {
  -webkit-tap-highlight-color: transparent;
}
input:focus {
  outline: none;
}
#app {
  position: relative;
  width: 100%;
  height: 100%;
  /*min-height: 100%;*/
  overflow: auto;
  font-family: 'Avenir', Helvetica, Microsoft YaHei, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f4f4f4;
  color: #36464e;
}
#app .mint-header.page-header {
    height: 7.85714rem;
    background-color: #1a1a1f;
}
#app .mint-header.page-header .mint-header-title {
      font-size: 16px;
}
#app .mint-header.page-header .search-box-container {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      padding: 0 1.71429rem;
}
#app .mint-header.page-header .search-box-container .search-box {
        width: 100%;
        height: 4rem;
        margin-top: 2rem;
        /*line-height: px2rem(75);*/
        opacity: .8;
        padding-left: 5rem;
        padding-right: 4.28571rem;
        border: none;
        border-radius: 20px;
}
#app .mint-header.page-header .search-box-container .search-icon {
        position: absolute;
        bottom: 2.57143rem;
        left: 3.14286rem;
        width: 2.14286rem;
        z-index: 1;
}
#app .mint-header.is-fixed + .page-main {
    top: 7.85714rem;
}
#app .page {
    position: relative;
    overflow: auto;
    height: 100%;
    width: 100%;
}
#app .mint-button--danger {
    background-color: #c30911;
}
.page-main {
  position: relative;
  height: auto;
  width: 100%;
  margin-bottom: 56px;
}
.page-main .scroll-outer-wrapper {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    background: transparent;
}
.bottom-single-btn {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 61px;
  background: #fff;
  padding: 10px;
}
.mint-toast {
  padding: 12px 15px !important;
}
.sub-title {
  padding: 8px 20px;
  color: #7c868c;
  font-size: .75rem;
  line-height: 14px;
}
.sub-content {
  margin: 10px 0 0;
  padding: 10px 20px;
  background-color: #ffffff;
}
.info-show .mint-cell .mint-cell-wrapper {
  background-image: linear-gradient(180deg, #e4eaec, #e4eaec 50%, transparent 50%);
}
.info-show .mint-cell .mint-cell-title {
  color: #a3afb7;
}
.mint-cell:last-child {
  background-image: none;
}
.mint-cell:first-child .mint-cell-wrapper {
  background-image: none;
}
.mint-indicator .mint-indicator-wrapper {
  z-index: 1000;
}
.noMore {
  padding: 15px 0 15px;
  text-align: center;
  color: #999999;
  font-size: .875rem;
}
.mint-cell .mint-cell-wrapper {
  font-size: 14px;
}
.mint-cell .mint-cell-wrapper .mint-cell-value .mint-field-core {
    text-align: left;
}
.mint-cell-not-border .mint-cell-wrapper {
  position: relative;
  background-image: none;
}
.mint-cell-not-border .mint-cell-wrapper:after {
    position: absolute;
    bottom: 0;
    left: 60px;
    right: 0;
    height: 1px;
    content: '';
    background-color: #e4eaec;
}
.mint-cell-not-border .mint-cell-wrapper:last-child:after {
    display: none;
}
.mint-cell-not-border .mint-cell-wrapper .mint-cell-title img {
    margin: 0 20px 0 10px;
}
.mint-cell-not-border .mint-cell-wrapper .mint-cell-title .mint-cell-text {
    color: #36464e;
    font-size: .9125rem;
}
.modal-bg {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.mint-navbar {
  border-bottom: 1px solid #dedede;
}
.mint-navbar .mint-tab-item {
    padding: 0;
}
.mint-navbar .mint-tab-item.is-selected {
      margin-bottom: 0;
      border-bottom: none;
      color: #40c4ff;
}
.mint-navbar .mint-tab-item.is-selected .mint-tab-item-label {
        position: relative;
        display: inline-block;
}
.mint-navbar .mint-tab-item.is-selected .mint-tab-item-label:after {
          position: absolute;
          left: -5px;
          right: -5px;
          bottom: 0;
          height: 2px;
          background-color: #40c4ff;
          content: '';
}
.mint-navbar .mint-tab-item .mint-tab-item-label {
      padding: 12px 0;
      font-size: 16px;
      line-height: 1.6;
}
* > .link {
  color: inherit !important;
}
.page-infinite-loading {
  margin-top: 0;
  text-align: center;
  height: 50px;
  line-height: 50px;
}
.page-infinite-loading .mint-spinner-fading-circle {
    display: inline-block;
    vertical-align: middle;
    margin-right: 5px;
}
.mint-tabbar .mint-tab-item {
  position: relative;
}
.mint-tabbar .mint-tab-item .vertical-line {
    position: absolute;
    right: 0;
    width: 1px;
    height: 100%;
    top: 0;
    background-color: #dedede;
}
.footer-btn {
  padding-top: 50px;
}
.footer-btn .footer-btn-box {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 8px 15px;
    background: #f4f9fc;
}
.line-btn {
  padding-bottom: 50px;
}
.line-btn .line-btn-box {
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #ffffff;
    text-align: center;
    font-size: 1rem;
    line-height: 40px;
    color: #37474f;
}
.line-btn .line-btn-box.red {
      color: #ff5353;
}
.line-btn .line-btn-box.green {
      color: #48d9a1;
}
.bzw-icon {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-repeat: no-repeat;
