}
.mescroll {
  position: absolute !important;
}
.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: #fff;
}
.no-data-container {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  text-align: center;
  /*vertical-align: middle;*/
}
.no-data-container .no-data-icon {
    position: absolute;
    width: 11.42857rem;
    top: -13.57143rem;
    left: 50%;
    margin-left: -5.71429rem;
}
.no-data-container p {
    font-size: 2.14286rem;
    opacity: .4;
}
.no-data-container.normal {
    position: relative;
    transform: translateY(0%);
}
.no-data-container.normal .no-data-icon {
      position: relative;
      width: 11.42857rem;
      top: 0;
      left: 0;
      margin-top: 1.42857rem;
      margin-left: 0;
      margin-bottom: 1.42857rem;
}
@keyframes fadeInRight {
from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
}
to {
    opacity: 1;
    transform: none;
}
}
.page.transition-module {
  background-color: #f4f4f4;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  animation: fadeInRight 0.4s ease 0s;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.clearfix:after {
  content: "";
  display: block;
  clear: both;
}
.sort-navbar.mint-navbar {
  position: absolute;
  width: 100%;
  top: 7.85714rem;
}
.sort-navbar.mint-navbar .mint-tab-item .mint-tab-item-label {
    width: 100%;
    height: 6.57143rem;
    padding: 0;
    line-height: 6.57143rem;
}
.sort-navbar.mint-navbar .mint-tab-item.is-selected .mint-tab-item-label {
    color: #c30911;
}
.sort-navbar.mint-navbar .mint-tab-item.is-selected .mint-tab-item-label::after {
      left: 0;
      right: 0;
      bottom: 0px;
      height: 1px;
      background-color: #c30911;
}
.colbfbfbf {
  color: #bfbfbf;
}
.col888888 {
  color: #888888;
}
.col1aad19 {
  color: #1aad19;
}
.colc30911 {
  color: #c30911;
}
.bgbfbfbf {
  background-color: #bfbfbf;
}
.bg1aad19 {
  background-color: #1aad19;
}
.bgc30911 {
  background-color: #c30911;
}
.child-view {
  position: absolute;
  width: 100%;
  transition: all 0.4s cubic-bezier(0.55, 0, 0.1, 1);
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  transform: translate(100%, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  transform: translate(-50px, 0);
}
.router-fade-enter-active, .router-fade-leave-active {
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}



